
import React, {useContext, useState, useEffect} from 'react'
import AuthContext from '../context/AuthContext'
import {Link, Redirect} from 'react-router-dom'
import PaystackPop from '@paystack/inline-js'


const PropertyDetailPage = (props) => {
	let {user, logoutUser} = useContext(AuthContext)
    const id = props.match.params.id

    const [propertyDetail, setPropertyDetail] = useState("")
    

    const getPropertyDetail = async () => {
        
        const response = await fetch(`http://127.0.0.1:8000/listings/${id}`,
        {
          method: 'GET',
          credentials: "include",
          headers: {
            "Content-Type": "application/json"
          },
          
        
        }
        )
        const data = await response.json()
        
        setPropertyDetail(data)
        
    
        console.log(data)
    
      }

      useEffect(() => {
		getPropertyDetail()
	  }, [])



      //booking a property

      const bookProperty = async () => {
        
        const response =  await fetch('http://127.0.0.1:8000/book-create/',{
          method: "POST",
          credentials: "include",
          headers: {
            "Accept": "application/json",
            "Content-Type": "application/json",
          },
          body:JSON.stringify({
            "user": user.user.pk,
            "listing": id,
            
            
        })
        })
  
        let data = await response.json()
        
       
      }



      // Processing payment.
    const paywithpaystack = (e) =>{
        e.preventDefault()
        const paystack = new PaystackPop()
        paystack.newTransaction({
          key:"pk_test_0019a4e3acb7a9886c8c65386a5be0619de6223e",
          amount: propertyDetail.price*100,    
          email: user.user.email,
          username: user.user.username,
  
          onSuccess(transaction){
            let message = `Payment Complete! Reference ${transaction.reference}`
            alert(message)
            
          },
          onSuccess: () => bookProperty(),
          
          onCancel(){
            alert("Transaction cancelled!")
          }
        })
  
       
     }
  


	return (
		<div class="container">
            
           
            
<div class="row ">
  <div class="col-sm property-card"><img class="" src={`${propertyDetail.image}`} width="460px" height="370px"/></div>
  <div class="col-sm property-card" >An image would be here</div>
  
</div> <hr/><br/>
<div class="row">
  <div class="col-sm-6">col-sm-8</div>
  <div class="col-sm-6">
    <section class="checkout-background">
  <div class=" py-5">
    <div class="row d-flex justify-content-center">
      <div class="">
        <div class="card rounded-6">
          <div class="card-body mx-1 my-2">
            <div class="d-flex align-items-center">
              <div>
                
              </div>
              <div>
                
              </div>
            </div>

            <div class="pt-3">
              <div class="d-flex flex-row pb-3">
                <div class="rounded border border-primary border-2 d-flex w-100 p-3 align-items-center checkout"
                  >
                  <div class="d-flex align-items-center pe-3">
                    <input class="form-check-input" type="radio" name="radioNoLabelX" id="radioNoLabel11"
                      value="" aria-label="..." checked />
                  </div>
                  <div class="d-flex flex-column">
                    <p class="mb-1 small text-primary">Amount to pay</p>
                    <h6 class="mb-0 text-primary">{propertyDetail.price}</h6>
                  </div>
                </div>
              </div>

              <div class="d-flex flex-row pb-3">
                
              </div>
            </div>

            <div class="d-flex justify-content-between align-items-center pb-1">
              
              <button onClick={paywithpaystack} type="button" class="btn btn-primary btn-lg">Book Now</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>
  </div>
</div>
        </div>
	  

		)
}
export default PropertyDetailPage