
import React, {useContext, useState, useEffect} from 'react'
import AuthContext from '../context/AuthContext'
import {Link} from 'react-router-dom'
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';


const HomePage = () => {
	let {user, logoutUser} = useContext(AuthContext)

	const [listings, setListings] = useState("")

	const getListings = async () => {
        
        const response = await fetch("http://127.0.0.1:8000/listings/",
        {
          method: 'GET',
          credentials: "include",
          headers: {
            "Content-Type": "application/json"
          },
          
        
        }
        )
        const data = await response.json()
        
        setListings(data)
        
    
        console.log(data)
    
      }

	  useEffect(() => {
		getListings()
	  }, [])





	return (
		<div class="wrapper">
			<Navbar bg="light" expand="lg" fixed="top">
      <Container fluid>
        <Navbar.Brand href="#">Navbar scroll</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <Nav.Link href="#action1">Sign in</Nav.Link>
            <Nav.Link href="#action2">Sign up</Nav.Link>
            <NavDropdown title="Filter" id="navbarScrollingDropdown">
              <NavDropdown.Item href="#action3">KNUST</NavDropdown.Item>
              <NavDropdown.Item href="#action4">
                UCC
              </NavDropdown.Item>
			  <NavDropdown.Item href="#action4">
                ASHESI
              </NavDropdown.Item>
			  <NavDropdown.Item href="#action4">
                UG
              </NavDropdown.Item>
			  <NavDropdown.Item href="#action4">
                UDS
              </NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action5">
                Something else here
              </NavDropdown.Item>
            </NavDropdown>
            
          </Nav>
          <Form className="d-flex">
            <Form.Control
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="outline-success">Search</Button>
          </Form>
        </Navbar.Collapse>
      </Container>
    </Navbar>
            
<br/>
<div class="hey">
	{listings && listings.map((listing, index)=>(
		<React.Fragment key = {index}>
			
			<Link to={`/property-detail/${listing.id}`}>
			<div className='property-card'>
			<img src={`${listing.image}`} width="305px" height="315px"/>
					<span>{listing.location}</span><p>{listing.number_of_persons} in a room</p><strong>{listing.price}</strong>
					
			</div></Link>
		</React.Fragment>
		))}
			
			
			
			
			
			
			
		
			
			
			
			
			</div>

        </div>
	  

		)
}
export default HomePage